.title {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px; }
  .title ::before {
    content: "";
    border-right: 4px solid #41f6d7;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    border-radius: 4px; }
  .title .title__h1 {
    font-family: TTLakes, sans-serif;
    font-weight: bold;
    font-size: 28px; }
  .title .title__menu {
    display: none; }
    @media (min-width: 1120px) {
      .title .title__menu {
        display: block; } }
  .title .title__link {
    transition: all 0.5s ease; }
    .title .title__link :first-child {
      margin-right: 32px; }
    .title .title__link a {
      display: flex;
      align-items: center;
      font-family: TTLakes, sans-serif;
      color: #1b1c21;
      font-size: 16px;
      text-decoration: none;
      font-weight: 500; }
      .title .title__link a img {
        margin-left: 8px; }
    .title .title__link:hover {
      opacity: 0.6; }
