@media (min-width: 1120px) {
  .main {
    display: grid;
    grid-template-areas: "aside main" "aside footer";
    grid-template-columns: 320px 1fr; } }

.main .main__disclaimer {
  font-family: TTLakes, sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #1b1c21;
  width: 100%;
  margin-left: 16px;
  margin-right: 16px; }
  @media (min-width: 768px) {
    .main .main__disclaimer {
      width: 420px; } }

.main .main__container {
  background-color: #f8f8f8;
  padding-top: 24px;
  padding-bottom: 40px;
  grid-area: main; }
  @media (min-width: 1120px) {
    .main .main__container {
      padding-top: 42px; } }

.main .main__offer {
  position: relative;
  margin-top: 16px;
  margin-left: 16px;
  font-family: TTLakes, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.5s ease; }
  .main .main__offer:hover {
    transform: translateX(45px) scale(1.1);
    opacity: 0.9; }
  .main .main__offer::after {
    content: "";
    background-image: url(./static/more.svg);
    width: 25px;
    height: 25px;
    position: absolute;
    margin-top: -2px; }
