.article {
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap; }
  .article .article__text {
    font-family: TTLakes, sans-serif;
    font-size: 14px; }
  .article .article__img-container {
    margin-top: 30px; }
    @media (min-width: 768px) {
      .article .article__img-container {
        margin-left: 24px;
        margin-right: 24px;
        margin-top: 0; } }
    @media (min-width: 1120px) {
      .article .article__img-container {
        margin-left: 18px;
        margin-right: 35px;
        margin-top: 0; } }
    .article .article__img-container .article__img {
      width: 100vw; }
      @media (min-width: 768px) {
        .article .article__img-container .article__img {
          width: 0;
          min-width: 360px; } }
      @media (min-width: 1120px) {
        .article .article__img-container .article__img {
          min-width: 530px; } }
  .article .article__container {
    flex-grow: 1;
    margin-left: 16px;
    margin-right: 16px; }
    @media (min-width: 768px) {
      .article .article__container {
        margin-left: 24px;
        margin-right: 24px; } }
    @media (min-width: 1120px) {
      .article .article__container {
        margin-left: 35px;
        margin-right: 18px; } }
  .article .article__text--size--small {
    display: block; }
    @media (min-width: 768px) {
      .article .article__text--size--small {
        display: none; } }
  .article .article__text--size--medium {
    display: none; }
    @media (min-width: 768px) {
      .article .article__text--size--medium {
        display: block; } }
    @media (min-width: 1120px) {
      .article .article__text--size--medium {
        display: none; } }
  .article .article__text--size--large {
    display: none; }
    @media (min-width: 1120px) {
      .article .article__text--size--large {
        display: block; } }
  @media (min-width: 768px) {
    .article {
      flex-wrap: nowrap; } }
