.menu {
  overflow: auto;
  margin-top: 24px;
  margin-left: 16px; }
  .menu ul {
    list-style-type: none;
    display: flex;
    align-items: center; }
    @media (min-width: 1120px) {
      .menu ul {
        flex-wrap: wrap; } }
  .menu .menu__item {
    flex-shrink: 0;
    transition: opacity 0.5s ease; }
    .menu .menu__item button {
      border: none;
      background: none;
      padding: 8px 16px;
      font-family: TTLakes, sans-serif;
      color: #7e7e82;
      font-size: 16px;
      font-weight: 500;
      cursor: pointer; }
    .menu .menu__item:hover {
      opacity: 0.6; }
  .menu .menu__item.menu__item--status--active button {
    background-color: #fff;
    border: 2px solid #b8ffec;
    border-radius: 6px; }
  .menu::-webkit-scrollbar {
    display: none; }
  @media (min-width: 768px) {
    .menu {
      margin-top: 32px;
      margin-left: 24px; } }
  @media (min-width: 1120px) {
    .menu {
      margin-top: 8px;
      margin-left: 32px; } }
