.header {
  background-color: #fff;
  padding: 20px 16px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d9fff5; }
  .header .header__main {
    display: flex;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    border-left: none;
    border-right: solid #eaeaea 2px;
    justify-content: space-between;
    width: 100%; }
    .header .header__main .header__logo {
      cursor: pointer;
      transition: all 0.5s ease; }
      .header .header__main .header__logo:hover {
        transform: scale(1.1);
        opacity: 0.9; }
    .header .header__main .list__link {
      margin: 0 8px;
      transition: all 0.5s ease; }
      .header .header__main .list__link:hover {
        transform: scale(1.1);
        opacity: 0.9; }
    @media (min-width: 768px) {
      .header .header__main {
        border-left: solid #eaeaea 2px; } }
  .header .header__burger {
    padding-right: 20px;
    transition: all 0.5s ease; }
    .header .header__burger:hover {
      transform: scale(1.1);
      opacity: 0.9; }
    .header .header__burger button {
      width: 40px;
      height: 40px;
      background-color: inherit;
      border: none;
      cursor: pointer; }
  .header .header__nav {
    padding-left: 20px; }
    .header .header__nav .list__link {
      transition: all 0.5s ease; }
      .header .header__nav .list__link:hover {
        transform: scale(1.1);
        opacity: 0.9; }
    .header .header__nav .list__link:first-child {
      margin-right: 16px; }
  .header .header__contact {
    display: none; }
    @media (min-width: 768px) {
      .header .header__contact {
        display: block; } }
  @media (min-width: 1120px) {
    .header {
      display: none; } }
